html, body {
  padding: 0;
  margin: 0;
}

body {
  color: #F9FFFF;
  background-color: #1A2541;
  font-family: "Brutal Type W00", sans-serif;
  font-size: 16px;
  font-weight: 400;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
}

.cols-3 {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2.5rem;
  row-gap: 2.5rem;
}
@media screen and (max-width: 47.9375rem) {
  .cols-3 {
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }
}

.section {
  position: relative;
  padding-top: 6.25rem;
  z-index: 1;
}
.section:not(.section__about) {
  padding-top: 11.25rem;
}
.section:last-of-type {
  padding-bottom: 11.25rem;
}
@media screen and (max-width: 74.9375rem) {
  .section {
    padding-top: 5rem;
  }
  .section .blured-figure {
    display: none;
    visibility: hidden;
  }
  .section:last-of-type {
    padding-bottom: 5rem;
  }
}
@media screen and (max-width: 35.5rem) {
  .section {
    padding-top: 3.75rem;
  }
  .section:not(.section__about) {
    padding-top: 3.75rem;
  }
  .section.section__trigger {
    padding-top: 5rem;
  }
  .section:last-of-type {
    padding-bottom: 5rem;
  }
}

@media screen and (min-width: 75rem) {
  .blured-figure {
    position: absolute;
    top: 0;
    background-image: url(../images/svg/blured-radial-figure.svg);
    background-repeat: no-repeat;
    animation: blured-figure-opacity 15s ease-in-out infinite;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
  .blured-figure.left {
    left: 0;
    background-position: left top;
  }
  .blured-figure.left.small {
    transform: translate(-15rem, 6.25rem);
  }
  .blured-figure.right {
    right: 0;
    background-position: right top;
  }
  .blured-figure.right.small {
    transform: translate(15rem, 6.25rem);
  }
  .blured-figure.small {
    background-size: 21.25rem;
  }
}
@keyframes blured-figure-opacity {
  0% {
    opacity: 0.4;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 0.4;
  }
}
.advantages {
