    transform: translate(-2.3125rem, -2.3125rem);
    height: 21.5rem;
    width: 21.5rem;
  }
}
@media screen and (max-width: 767px) {
  .trigger__image {
    margin: 0 auto 3.75rem;
    height: 12rem;
    width: 12rem;
  }
  .trigger__image::before {
    transform: translate(-0.8125rem, -0.8125rem);
    height: 13.625rem;
    width: 13.625rem;
  }
  .trigger__image::after {
    transform: translate(-1.625rem, -1.625rem);
    height: 15.25rem;
    width: 15.25rem;
  }
}

.modal-window {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 999;
  transition: all 0.5s cubic-bezier(0.02, 1.23, 0.79, 1.08);
}
.modal-window.opened {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.modal-window.opened .modal-window__content {
  opacity: 1;
  transform: scale(1) translate(-50%, -50%);
}
.modal-window__body {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgba(26, 37, 65, 0.95);
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
}
.modal-window__content {
  position: absolute;
  top: 50%;
  left: 50%;
  color: #1A2541;
  background-color: #F9FFFF;
  border-radius: 1.5rem;
  padding: 1.875rem;
  width: 25rem;
  opacity: 0;
  transform: scale(0.8) translate(calc(-50% - 3.125rem), calc(-50% + 3.125rem));
  transition: all 0.5s cubic-bezier(0.02, 1.23, 0.79, 1.08);
  transition-duration: 0.5s;
  transition-delay: 0.2s;
}
.modal-window h1 {
  font-size: 1.5em;
  margin: 0 0 0.9375rem;
  line-height: normal;
}

.modal-close {
  color: #8A93A0;
  line-height: 3.125rem;
  font-size: 80%;
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
  width: 4.375rem;
  text-decoration: none;
}
.modal-close:hover {
  color: #1A2541;
}